"use client";

import { PageHeader } from "@/components/layout/PageHeader";
import { Panel } from "@/components/shared/Panel";
import { MemorySearch } from "@/components/memory/MemorySearch";
import { MemoryResults } from "@/components/memory/MemoryResults";
import { StoreFactForm } from "@/components/memory/StoreFactForm";
import { useMemory } from "@/hooks/useMemory";

export default function MemoryPage() {
  const { results, loading, error, search, storeFact } = useMemory();

  return (
    <div>
      <PageHeader
        title="Memory"
        subtitle="Search agent memory and store new facts"
      />

      <div className="space-y-6">
        <Panel>
          <h2 className="text-sm font-heading font-semibold text-white/70 uppercase tracking-wider mb-4">
            Search Memory
          </h2>
          <MemorySearch onSearch={search} loading={loading} />
          {error && (
            <div className="mt-3 text-xs text-danger">{error}</div>
          )}
          <div className="mt-4">
            <MemoryResults results={results} loading={loading} />
          </div>
        </Panel>

        <StoreFactForm onStore={storeFact} />
      </div>
    </div>
  );
}
