"use client";

import { useState, useCallback, type FormEvent } from "react";
import { Input } from "@/components/shared/Input";
import { Button } from "@/components/shared/Button";

interface MemorySearchProps {
  onSearch: (query: string, types?: string[]) => void;
  loading?: boolean;
}

const MEMORY_TYPES = ["semantic", "episodic", "procedural"] as const;

export function MemorySearch({ onSearch, loading }: MemorySearchProps) {
  const [query, setQuery] = useState("");
  const [activeTypes, setActiveTypes] = useState<Set<string>>(new Set());

  const toggleType = useCallback((type: string) => {
    setActiveTypes((prev) => {
      const next = new Set(prev);
      if (next.has(type)) {
        next.delete(type);
      } else {
        next.add(type);
      }
      return next;
    });
  }, []);

  const handleSubmit = useCallback(
    (e: FormEvent) => {
      e.preventDefault();
      if (!query.trim()) return;
      const types = activeTypes.size > 0 ? [...activeTypes] : undefined;
      onSearch(query.trim(), types);
    },
    [query, activeTypes, onSearch]
  );

  return (
    <form onSubmit={handleSubmit} className="space-y-3">
      <div className="flex gap-3">
        <div className="flex-1">
          <Input
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Search memory..."
          />
        </div>
        <Button type="submit" disabled={loading || !query.trim()}>
          {loading ? "Searching..." : "Search"}
        </Button>
      </div>
      <div className="flex gap-2">
        {MEMORY_TYPES.map((type) => (
          <button
            key={type}
            type="button"
            onClick={() => toggleType(type)}
            className={`px-3 py-1 text-xs rounded-pill border transition-colors ${
              activeTypes.has(type)
                ? type === "semantic"
                  ? "border-accent-blue bg-accent-blue/15 text-accent-blue"
                  : type === "episodic"
                    ? "border-accent-magenta bg-accent-magenta/15 text-accent-magenta"
                    : "border-success bg-success/15 text-success"
                : "border-surface-border text-white/40 hover:text-white/60"
            }`}
          >
            {type}
          </button>
        ))}
      </div>
    </form>
  );
}
