"use client";

import type { MemoryResult } from "@/lib/types";
import { Tag } from "@/components/shared/Tag";

interface MemoryEntryProps {
  memory: MemoryResult;
}

const typeBorder: Record<string, string> = {
  semantic: "border-accent-blue",
  episodic: "border-accent-magenta",
  procedural: "border-success",
};

export function MemoryEntry({ memory }: MemoryEntryProps) {
  return (
    <div
      className={`border-l-2 ${typeBorder[memory.type] || "border-white/20"} pl-4 py-3`}
    >
      <div className="flex items-center gap-2 mb-1">
        <Tag
          variant={
            memory.type === "semantic"
              ? "info"
              : memory.type === "episodic"
                ? "default"
                : "success"
          }
        >
          {memory.type}
        </Tag>
        <span className="text-2xs text-white/30">
          Score: {typeof memory.score === "number" ? memory.score.toFixed(2) : "—"}
        </span>
        {memory.importance !== undefined && (
          <span className="text-2xs text-white/30">
            Importance: {memory.importance}
          </span>
        )}
      </div>
      <div className="text-sm text-white/80 mb-1">{memory.content}</div>
      {memory.tags && memory.tags.length > 0 && (
        <div className="flex flex-wrap gap-1">
          {memory.tags.map((tag) => (
            <span
              key={tag}
              className="text-2xs px-1.5 py-0.5 rounded bg-white/5 text-white/40"
            >
              {tag}
            </span>
          ))}
        </div>
      )}
    </div>
  );
}
